import React from 'react';
import axios from 'axios';
import {
 FormGroup,
 FormLabel,
 Form,
 FormControl,
 Button
 } from 'react-bootstrap';

class CategoryForm extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            category_id: null
        };
    }


    componentDidMount() {
        // Simple POST request with a JSON body using axios
        const category = { title: 'React POST Request Example' };
        axios.post('http://127.0.0.1:8000/api/v1/category/', category)
            .then(response => this.setState({
            category_title: response.category_title,
            category_description: response.category_description,
            }));
    }

  render() {
    return (
    <div className="container">
      <Form method='post' onSubmit={this.createCategory}>
          <h2 className="text-center">Create a Category</h2>
          <Form.Group className="mb-3" controlId="formBasicCategory">
            <Form.Label>Category</Form.Label>
            <Form.Control name="category_title" type="text" placeholder="Enter a category" />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicDescription">
            <Form.Label>Description</Form.Label>
            <Form.Control name="category_description" type="textarea" placeholder="..." />
          </Form.Group>
          <Button variant="primary" type="submit">
            Create
          </Button>
        </Form>
    </div>
    )
  }
}

export default CategoryForm;